<template>
  <v-col cols="12" lg="6" md="6" sm="12" xl="6">
    <v-img class="align-end" :src="mountUrlImg(item)" contain height="15rem" />
  </v-col>
  <v-col id="col_content" cols="12" lg="6" md="6" sm="12" xl="6">
    <h3>{{ item.displayName }}</h3>
    <div>
      <h4>Descrição:</h4>
      <span>{{ item.description }}</span>
    </div>
    <div id="line_info">
      <h4>Linguagem:</h4>
      <span>
        {{ item.language }}
        <v-icon>{{ item.displayIconLanguage }}</v-icon>
      </span>
    </div>
    <div id="line_info">
      <h4>Criado em:</h4>
      <span>{{ item.displayCreate }}</span>
    </div>
    <div id="line_info">
      <h4>Ultima atualização:</h4>
      <span> {{ item.displayLastUpdate }}</span>
    </div>
    <v-btn
      rounded
      class="ma-2"
      outlined
      color="indigo"
      @click="redirect(item.html_url)"
    >
      <v-icon dark> mdi-arrow-right </v-icon>Ir ao repositório
    </v-btn>
  </v-col>
</template>
<script>
export default {
  name: "itensProjetos",

  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  data: () => ({}),
  methods: {
    mountUrlImg(info) {
      let url = `https://raw.githubusercontent.com/${info.owner.login}/${info.name}/master/${info.name}.png`;
      return url;
    },
    redirect(url) {
      window.open(`${url}`, "_blank");
    },
  },
};
</script>
