<template>
  <v-container style="margin-top: 65px">
    <v-row id="linha">
      <v-col v-for="(item, key) in dados" :key="item.id">
        <!-- ---------------------------------------------------- -->
        <!--                 Exibição com duas cores              -->
        <!-- ---------------------------------------------------- -->
        <v-row id="style_row" v-if="key % 2 === 0 || larguraTela === false">
          <ItensProjetos :item="item" />
        </v-row>
        <!-- ---------------------------------------------------- -->
        <!--                 Exibição com cores unica             -->
        <!-- ---------------------------------------------------- -->
        <v-row v-if="key % 2 !== 0 && larguraTela === true">
          <ItensProjetos :item="item" />
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import ItensProjetos from "../components/ItensProjetos.vue";
export default {
  name: "ProjetosPage",
  components: { ItensProjetos },
  data: () => ({
    dados: [],
    larguraTela: false,
  }),
  methods: {
    CheckWindow() {
      setInterval(() => {
        if (window.screen.width > 960) {
          this.larguraTela = true;
        } else {
          this.larguraTela = false;
        }
      }, 100);
      // Verificar uma parada para esta função
    },
    iconsLanguage(language) {
      let list = [
        "HTML",
        "C",
        "Jupyter Notebook",
        "JavaScript",
        "CSS",
        "Shell",
        "C++",
        "C#",
        "GO",
        "PHP",
        "Python",
        "TypeScript",
        "PLpgSQL",
      ];
      switch (language) {
        case list[0]:
          return "mdi-language-html5";
        case list[1]:
          return "mdi-language-c";
        case list[2]:
          return "mdi-language-python";
        case list[3]:
          return "mdi-language-javascript";
        case list[4]:
          return "mdi-language-css3";
        case list[5]:
          return "mdi-console";
        case list[6]:
          return "mdi-language-cpp";
        case list[7]:
          return "mdi-language-csharp";
        case list[8]:
          return "mdi-language-go";
        case list[9]:
          return "mdi-language-php";
        case list[10]:
          return "mdi-language-python";
        case list[11]:
          return "mdi-language-typescript";
        default:
          return "mdi-null";
      }
    },
  },
  async mounted() {
    if (this.$store.state.repositorios < 1) {
      await this.$store.dispatch("getRepositorios");
    }
    await this.$store.state.repositorios.map((item) => {
      if (item.has_projects === true) {
        item.displayName =
          item.name.charAt(0).toUpperCase() +
          item.name.slice(1).split("_").join(" ");
        item.displayCreate = item.created_at
          .split("T")[0]
          .split("-")
          .reverse()
          .join("/");
        item.displayLastUpdate = item.updated_at
          .split("T")[0]
          .split("-")
          .reverse()
          .join("/");
        item.displayIconLanguage = this.iconsLanguage(item.language);
        this.dados.push(item);
      }
    });
    this.CheckWindow();
  },
};
</script>

