  display: flex;
  flex-direction: column;
}
#style_row {
  background-color: hsla(244, 100%, 50%, 0.103);
  border-style: hidden;
  border-radius: 3px;
  margin-bottom: 1.5px;
}
#col_content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
h3 {
  text-align: center;
  margin-bottom: 15px;
}
#line_info {
  display: flex;
  justify-content: space-between;
}
</style>
